import '../styles/ViewAllProducts.css';
import { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { IoCart } from 'react-icons/io5';
import Currency from '../components/Currency';
import {Checkbox} from 'antd';

const ViewAllProducts = () => {
    const [products, setProducts] = useState([]);
    const [categories, setCategories] = useState([]);
    const [checked, setChecked] = useState([]);
    const [total, setTotal] = useState(0);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(false)


    // get all products
    const getAllProducts = async () => {
        try {
          setLoading(true);
          const {data} = await axios.get(`/api/v1/product/product-list/${page}`)
          setLoading(false)
          setProducts(data.products);
        } catch (error) {
          setLoading(false)
        }
    }

    useEffect(() => {
        getAllProducts();
    },[])

     // get all category
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/category/allcategory`
      );

        setCategories(data?.category);

    } catch (error) {
    }
  };

  useEffect(() => {
    getAllCategory();
    getTotal();
  }, []);


  // get total count
  const getTotal = async () => {
    try {
      const {data} = await axios.get(`/api/v1/product/product-count`)
      setTotal(data?.total)
    } catch (error) {
    }
  }

//   filter by category
  const handleFilter = (value, id) => {
    let all = [...checked]
    if(value){
        all.push(id)
    }else{
        all = all.filter(c => c!== id)
    }
    setChecked(all)
  }

  useEffect(() => {
    if(!checked.lenght) getAllCategory();
  }, [checked.lenght]);
  
  useEffect(() => {
    if(checked.length) filterProducts();
  },[checked])

  // filter products
  const filterProducts = async () => {
    try {
      const {data} = await axios.post(`/api/v1/product/product-filter`,{checked})
      setProducts(data?.products)
    } catch (error) {
    }
  }

  useEffect(() => {
    if(page === 1) return;
    loadMore()
  },[page])

  // loadmore
  const loadMore = async () => {
    try {
      setLoading(true) 
      const {data} = await axios.get(`/api/v1/product/product-list/${page}`)
      setLoading(false)
      setProducts([...products, ...data?.products])
    } catch (error) {
      setLoading(false)
    }
  }

  return (
    <>
      <div className='all-product-page'>
        <div className='all-product-fillter'>
            <h3>All Categories</h3>
            <div className='fillter-cate'>
                {categories?.map(c => (
                    <Checkbox className='filter-check' key={c._id} onChange={(e) => handleFilter(e.target.checked,c._id)}>
                        {c.name}
                    </Checkbox>
                ))}
            </div>
            <button className='clear-btn' onClick={() => window.location.reload()}>
                  clear filter
            </button>
        </div>

        <div className='all-product-cont'>
            <h3>All Products</h3>
            <div className='all-products'>
            {products?.map(p => {
              return (
                <>
                <Link to={`/singlepage/${p.slug}`} key={p._id} className='card-link'>
                  <div className="card">
                    <div className="card-img">
                      <img src={`/api/v1/product/product-photo/${p._id}`} alt={p.name} />
                    </div>
                    <div className="card-info">
                      <p className="text-title"> {p.name} </p>
                      <p className="text-body"> {p.description.substring(0,30)}... </p>
                    </div>
                    <div className="card-footer">
                      <span className="text-title"> {<Currency price={p.price} />} </span>
                      <Link className='card-button-link'>
                        <div className="card-button">
                          <IoCart className='cart-icon'/>
                        </div>
                      </Link>
                    </div>
                  </div>
                </Link>
                </>
              )
            })}
            </div>
          <div className='load-more'>
            {products && products.length < total && (
              <button className='load-more-btn' onClick={(e) => {
                  e.preventDefault();
                  setPage(page + 1);
              }}>
                {loading ? "Loading..." : "Loadmore" }
              </button>
            )}
          </div>
        </div>
      </div>
    </>
  )
}

export default ViewAllProducts
